.slider-container{
  background: url('../../assets/images/background.jpg');
  background-size: cover;
  position: relative;
  background-position: bottom left;
  height: 100vh;
  width: 900vw;
  overflow-x: scroll;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.arrow-left{
  position: fixed;
  left: 0;
  top: 50%;
  padding: 1rem;
  cursor: pointer;
  z-index: 666;
}

.arrow-right{
  position: fixed;
  right: 0;
  top: 50%;
  padding: 1rem;
  cursor: pointer;
  z-index: 666;
}

.slider-item{
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-align: center;
      align-items: center;
}

.slider-item-title{
  display: -ms-flexbox;
  display: flex;
  color: white;
  font-size: 600;
  text-decoration: underline;
  font-size: 4rem;
  padding: 1rem 6rem;
  max-width: 35%;
  text-transform: uppercase;
  text-shadow: 0px 0px 30px rgba(0, 0, 0, 1);
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}
.slider-item-title:hover{
  text-shadow: 0px 0px 60px rgba(0, 0, 0, 1);
}

.vertical-center{
}

.slider-navigation{
  position: fixed;
  left: 0;
  bottom: 0;
  padding: 2rem 4rem;
  color: white;
}

.slider-navigation-item{
  display: inline-block;
  border: 1px dashed white;
  width: 25px;
  height: 25px;
  text-align: center;
  vertical-align: middle;
  -webkit-transition: all 1s ease;
  -o-transition: all 1s ease;
  transition: all 1s ease;
  cursor: pointer;
}

.active{
  background: white;
  color: #101010;
}



